

.users {
    padding-left: 34px;
    background-color: #fff;
    &__title {
        margin: 18px 0;
        font-weight: 700;
        font-size: 14px;
        line-height: 16px;
    }
    &__container {
        max-width: 420px;
        padding-bottom: 25px;
    }
}

.user {

    
    width: 100%;
    background-color: #F3F3F3;
    border-radius: 8px;
    padding: 10px;
    margin-bottom: 14px;

    display: flex;
    justify-content: space-between;
    &__info {
        display: flex;
        flex-direction: column;
        gap: 7px;
        &-block {
            display: flex;
            gap: 7px;
            font-weight: 400;
            font-size: 11px;
            line-height: 13px;
            color: #A3A3A3;
        }
        &-name, &-city, &-company {
            color: #000000;
        }
    }
    &__more {
        display: flex;
        flex-direction: column;
        justify-content: end;

        cursor: pointer;
        a {
            text-decoration: none;
            font-weight: 400;
            font-size: 12px;
            line-height: 14px;
            color: #4B51EF;
        }
    }
}

.totalUsers {
    display: flex;
    justify-content: end;
}