.sort {
    background-color: #BDBDBD;
    padding-left: 20px;
    padding-top: 35px;
    &__title {
        font-weight: 400;
        font-size: 12px;
        line-height: 14px;
        color: #000000;
    }
    &__btn {
        margin-top: 10px;
        background: #4B51EF;
        border-radius: 5px;
        border: none;
        padding: 5px 15px;
        cursor: pointer;
        font-weight: 400;
        font-size: 12px;
        line-height: 14px;
        color: #FFFFFF;
    }
}