.profile {
    background-color: #fff;
    padding: 19px 41px 19px 36px;
    &__container {
        background-color: #fff;
    }
    &__header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 10px;
    }
    &__title {
        display: inline-block;
        font-weight: 700;
        font-size: 14px;
        line-height: 16px;
        color: #000000;
    }
    &__edit {
        background: #4B51EF;
        border-radius: 5px;
        padding: 6px 11px;
        color: #fff;
        border: none;
        cursor: pointer;
    }
    &__form {
        padding: 20px 12px;
        border: 1px solid #F3F3F3;
        border-radius: 8px;
        input, textarea {
            background: #FFFFFF;
            border: 1px solid #D8D8D8;
            box-sizing: border-box;
            border-radius: 5px;
            padding: 5px 10px;
            max-width: 211px;
            width: 100%;
            font-size: 10px;
            line-height: 12px;
            color: rgba(0, 0, 0, 0.3);
            margin-top: 3px;
            display: block;
        }
        textarea {
            max-width: 418px;
            min-height: 55px;
            max-height: 55px;
            min-width: 418px;
        }
        &-label {
            display: block;
            font-weight: 400;
            font-size: 9px;
            line-height: 9px;
            margin-bottom: 10px;
        }
        &-send {
            background: #AFAFAF;
            border-radius: 5px;
            padding: 5px 15px;
            color: #fff;
            border: none;
            cursor: pointer;
            margin-top: 10px;
            transition: background .1s;
            &:hover {
                background: #757474;
            }
        }
    }
}

.form__button {
    display: flex;
    align-items: center;
    justify-content: space-between;
    a {
        text-decoration: none;
        font-weight: 400;
        font-size: 12px;
        line-height: 14px;
    }
}